}

.jumptron-logo-img{
  text-align: center;
}
.products-menu{
  justify-content: center;
}
@media (max-width: 576px){
  .products-menu{
    padding-left: 20px;
    padding-right: 20px;
  }
}
.align-text-center{
  text-align: center;
}
.img-leader-aboutus{
  border-radius: 90px;
}
.product-price-menu-item{
  text-align: center;
  color:rgb(101, 114, 114);
}


.page-enter {
  opacity: 0.01;
  transform: translateX(-100%);
}

.page-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: all 300ms ease-in;
}

.page-exit {
  opacity: 1;
  transform: translateX(0%);
}

.page-exit-active {
  opacity: 0.01;
  transform: translateX(100%);
  transition: all 300ms ease-out;
}
.product-details-img{
  height: 250px;
}

@media (max-width:768px){
  .product-details-img{
    height: 170px;
  }
  
}

@media (max-width:576px){
  .product-details-img{
    height: 250px;
  }
  .header-title{
    text-align: center;
  }
  .header-subtitle{
    text-align: center;
  }
}

.aboutus-leader-description{
