.signup-show-password-btn{
        width: 4.5%;
        height: 90%;     
        top: 4%;       
        position: absolute;
        left:95%;
        border-radius: 5px;
        background-color: white;
        border-width: 0px;
        padding: 0px;
        outline: 0;

}
.signup-show-password-btn:enabled{
    border-color: white;
    outline: 0;
}
.signup-password-field{
        width: 100%;
        height: 38px;            
        position: absolute;
        top: 0;
        left: 0;
}

.signup-password-container{
    width: 100%;
    height: 40px;
    position: relative;
}
@media only screen and (max-width: 500px) {
  .signup-show-password-btn{
    left: 92%;
    height: 87%;
  }
}

